.filter-checkbox {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 28px;
    margin-left: 48px;
}

.filter-checkbox__button {
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    margin-top: 4px;
}

.filter-checkbox__label {
    margin-left: 13px;
    font: 400 12px Inter, Arial, sans-serif;
    color: rgba(255, 255, 255, 1);
    white-space: pre-wrap;
}

@media (max-width: 740px) {
    .filter-checkbox {
        margin: 47px 0 0;
    }
}